<template>
    <div>
        <div class="card centered"
             v-if="loading">
            <app-pulse-loader></app-pulse-loader>
        </div>

        <div class="card background"
             v-if="!loading">
            <template v-for="batch in batches">
                <div class="card bg-light mb-3">
                    <div class="card-header"
                         @click="batch.expand=!batch.expand"
                         style="cursor: pointer; font-size: x-large">{{batch.name}}</div>
                    <div class="card-body" v-if="batch.expand">
                        <app-batch-result :batch-name="batch.name"></app-batch-result>
                    </div>
                </div>

            </template>
        </div>
    </div>

</template>

<script>
    import BatchResult from './BatchResult';
    import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
    import axios from "axios";

    const baseURL='https://spam-tutorial-result.firebaseio.com/';

    export default {
        name: 'Result',
        data(){
            return {
                loading: true,
                batches: []
            }
        },
        components: {
            appBatchResult:BatchResult,
            appPulseLoader: PulseLoader
        },
        created() {
            this.getAllBatches();
        },
        methods: {
            getAllBatches(){
                this.batches=[];
                const link=baseURL+"years.json";
                //console.log(link);
                axios.get(link)
                    .then(res=>{
                        for(let key in res.data){
                            let batch={
                                name:'',
                                expand: false
                            };
                            batch.name=res.data[key].year;
                            this.batches.push(batch);
                        }

                        this.batches=this.batches.reverse();

                        this.loading=false;
                        //console.log(this.chapters);
                    }).catch(error=>{
                    //console.log(error);
                });
            }
        }
    }
</script>

