<template>
    <div>
        <div class="card centered"
             v-if="loading">
            <app-pulse-loader></app-pulse-loader>
        </div>

        <div v-if="!loading">
            <!--    New Result-->
            <div class="alert alert-warning"
                 v-if="recentResults.length>0">

                <div class="alert-heading">
                    <strong style="font-size: x-large">!!!New result</strong>
                </div>

                <div class="card text-white bg-success mb-3"
                     v-for="result in recentResults">
                    <div class="card-header"
                         style="font-family: 'Galada', cursive">
                        {{result.resultTitle}}
                        <a :href="result.downloadLink"
                           style="color: black"
                           target="_blank"
                           class=" float-right">
                            <svg class="bi bi-download"
                                 width="1.3em" height="1.3em"
                                 viewBox="0 0 16 16"
                                 fill="currentColor"
                                 xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" d="M.5 8a.5.5 0 01.5.5V12a1 1 0 001 1h12a1 1 0 001-1V8.5a.5.5 0 011 0V12a2 2 0 01-2 2H2a2 2 0 01-2-2V8.5A.5.5 0 01.5 8z" clip-rule="evenodd"/>
                                <path fill-rule="evenodd" d="M5 7.5a.5.5 0 01.707 0L8 9.793 10.293 7.5a.5.5 0 11.707.707l-2.646 2.647a.5.5 0 01-.708 0L5 8.207A.5.5 0 015 7.5z" clip-rule="evenodd"/>
                                <path fill-rule="evenodd" d="M8 1a.5.5 0 01.5.5v8a.5.5 0 01-1 0v-8A.5.5 0 018 1z" clip-rule="evenodd"/>
                            </svg>
                        </a>
                    </div>
                </div>

            </div>

            <!--    Old Result-->
            <div v-if="results.length>0">
                <div class="card text-white bg-success mb-3"
                     v-for="result in results">
                    <div class="card-header"
                         style="font-family: 'Galada', cursive">
                        {{result.resultTitle}}
                        <a :href="result.downloadLink"
                           style="color: black"
                           target="_blank"
                           class=" float-right">
                            <svg class="bi bi-download"
                                 width="1.3em" height="1.3em"
                                 viewBox="0 0 16 16"
                                 fill="currentColor"
                                 xmlns="http://www.w3.org/2000/svg">
                                <path fill-rule="evenodd" d="M.5 8a.5.5 0 01.5.5V12a1 1 0 001 1h12a1 1 0 001-1V8.5a.5.5 0 011 0V12a2 2 0 01-2 2H2a2 2 0 01-2-2V8.5A.5.5 0 01.5 8z" clip-rule="evenodd"/>
                                <path fill-rule="evenodd" d="M5 7.5a.5.5 0 01.707 0L8 9.793 10.293 7.5a.5.5 0 11.707.707l-2.646 2.647a.5.5 0 01-.708 0L5 8.207A.5.5 0 015 7.5z" clip-rule="evenodd"/>
                                <path fill-rule="evenodd" d="M8 1a.5.5 0 01.5.5v8a.5.5 0 01-1 0v-8A.5.5 0 018 1z" clip-rule="evenodd"/>
                            </svg>
                        </a>
                    </div>
                </div>

            </div>

        </div>

    </div>
</template>

<script>
    import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
    import axios from "axios";

    const baseURL='https://spam-tutorial-result.firebaseio.com/';

    export default {
        name: 'BatchResult',
        props: ['batchName'],
        components: {
            appPulseLoader: PulseLoader
        },
        data(){
            return {
                loading: true,
                results: [],
                recentResults: []
            }
        },
        created() {
            this.fetchAllResult();
        },
        methods: {
            fetchAllResult(){
                this.results=[];
                this.recentResults=[];

                const link=baseURL+this.batchName+".json";

                axios.get(link)
                    .then(res=>{
                        for(let key in res.data){
                            let result={
                                resultTitle: '',
                                downloadLink: ''
                            };
                            result.resultTitle=res.data[key].resultTitle;
                            result.downloadLink=res.data[key].downloadLink;
                            this.results.push(result);

                            let d=new Date(res.data[key].alertDate);
                            if(d>(new Date()))
                                this.recentResults.push(result);
                        }

                        this.loading=false;

                    }).catch(error=>{
                    //console.log(error);
                });
            }
        }
    }
</script>

<style scoped>

</style>
